<script lang="ts" setup>
/**
 * 
 *
 * @author libin
 * @date 20240615 11:58:57
 */

import { Ref, onMounted, ref } from 'vue'

const { flat } = withDefaults(
    defineProps<{
        flat?: number
    }>(),
    { flat: 100 }
)

const emiter = defineEmits<{
    (e: 'loaded'): void
    (e: 'completed'): void
}>()
/**  */
const status: Ref<'load' | 'flat' | 'loaded' | 'fail'> = ref('load')
/**  */
let start: number = Date.now()

/**  */
const onLoad = (): void => {
    status.value = flat && Date.now() - start < flat ? 'loaded' : 'loaded'
    emiter('loaded')
    emiter('completed')
}

/**  */
const onError = (): void => {
    status.value = 'fail'
    emiter('completed')
}

onMounted((): void => {
    start = Date.now()
})
</script>

<template>
    <img v-bind="$attrs" :class="['image', status]" @load="onLoad" @error="onError" />
</template>

<style lang="less" scoped>
.image.load {
    opacity: 0;
}
.image.flat,
.image.loaded {
    opacity: 1;
}
.image.loaded {
    transition: opacity 0.15s linear;
}
.image.fail {
    // , ~
    opacity: 1;
}
</style>
